*::-webkit-scrollbar {
    width: 5px;
    height:5px;
}

*::-webkit-scrollbar-track {
    box-shadow: inset 0 0 6px rgba(0, 0, 0, 30%);
}

*::-webkit-scrollbar-thumb {
    background-color: #455a64;
    outline: 1px solid #455a64;
    border-radius: 10px;
}

.MuiGrid-item{
    width: inherit;
}